import React from 'react';
import PropTypes from 'prop-types';
import {magic, encodeForUrl, KNEX_TYPES, MONACO_THEME, EDITOR_OPTIONS} from '@utils';
import {useLocation, useNavigate} from 'react-router-dom';
import Editor, {useMonaco} from '@monaco-editor/react';
import SplitPane from 'react-split-pane';
import OptionsBar from './OptionsBar';
import logo from '@assets/knex-logo.png';
import s from './style.scss';

const MainPage = ({
  setRef,
  isSmall,
  code,
  setCode,
  sql,
  setSql,
  client,
  getDataFromPath,
  formatTypes
}) => {
  const monaco = useMonaco();
  const location = useLocation();
  const navigate = useNavigate();

  React.useEffect(() => {
    if (monaco) {
      monaco
        .languages
        .typescript
        .typescriptDefaults
        .addExtraLib(formatTypes(KNEX_TYPES), 'knex.d.ts');

      monaco.editor.defineTheme('customTheme', MONACO_THEME);
      monaco.editor.setTheme('customTheme');
      monaco.editor.addEditorAction({
        id: 'prettierActivate',
        label: 'Makes your code more pretty',
        keybindings: [
          monaco.KeyMod.CtrlCmd |
          monaco.KeyMod.Alt |
          monaco.KeyCode.KeyL
        ],
        run: (editor) => {
          const action = editor.getAction('editor.action.formatDocument');

          return action.run(editor);
        }
      });
    }
  }, [monaco]);

  React.useEffect(() => {
    getDataFromPath(location);

  }, [location.search, location.hash]);

  const onChange = (newCode) => {
    setCode(newCode);

    const queryParams = new URLSearchParams();

    queryParams.set('c', client.id);

    navigate({
      search: queryParams.toString(),
      hash: encodeForUrl(newCode ?? code)
    });
  };

  return (
    <div
      className={s.container}
      ref={setRef}
    >
      <SplitPane split={isSmall ? 'horizontal' : 'vertical'}>
        <Editor
          theme={'customTheme'}
          language={'typescript'}
          value={code}
          onChange={onChange}
          options={EDITOR_OPTIONS.JS}
        />
        <Editor
          theme={'customTheme'}
          language={'sql'}
          value={sql}
          onChange={setSql}
          options={EDITOR_OPTIONS.SQL}
        />
      </SplitPane>
      <OptionsBar />
      <img
        src={logo}
        className={s.invisible}
      />
    </div>
  );
};

const mapStore = ({EditorStore}) => {
  return {
    setRef: EditorStore.setRef,
    isSmall: EditorStore.isSmall,
    code: EditorStore.code,
    setCode: EditorStore.setCode,
    sql: EditorStore.sql,
    setSql: EditorStore.setSql,
    client: EditorStore.client,
    getDataFromPath: EditorStore.getDataFromPath,
    formatTypes: EditorStore.formatTypes
  };
};

MainPage.propTypes = {
  setRef: PropTypes.func,
  isSmall: PropTypes.bool,
  code: PropTypes.string,
  setCode: PropTypes.func,
  sql: PropTypes.string,
  setSql: PropTypes.func,
  client: PropTypes.string,
  getDataFromPath: PropTypes.func,
  formatTypes: PropTypes.func
};

export default magic(MainPage, {store: mapStore, styles: s});