import React from 'react';
import {Routes, Route} from 'react-router-dom';
import {publicRoutes} from './routes';

const route = ({path, element, exact = false}) => (
  <Route
    key={path}
    path={path}
    element={element}
    exact={`${exact}`}
  />
);

const Router = () => (
  <Routes>
    {
      publicRoutes.map(route)
    }
  </Routes>
);

export default Router;