import React from 'react';
import PropTypes from 'prop-types';
import {magic, KNEX_CLIENTS} from '@utils';
import knexInfo from '../../../node_modules/knex/package.json';
import knexIcon from '@assets/knex-icon-white.png';
import github from '@assets/github.png';
import pg from '@assets/pg.png';
import mysql from '@assets/mysql.png';
import cockroachdb from '@assets/cockroachdb.png';
import redshift from '@assets/redshift.png';
import sqlite3 from '@assets/sqlite3.png';
import oracledb from '@assets/oracledb.png';
import mssql from '@assets/mssql.png';
import s from './style.scss';

const clientIcons = {
  pg,
  mysql,
  cockroachdb,
  redshift,
  sqlite3,
  oracledb,
  mssql
};

const OptionsBar = ({client, setClient}) => (
  <div className={s.optionsBar}>
    <a
      href={'https://knexjs.org/'}
      rel={'external noopener noreferrer'}
      target={'_blank'}
    >
      <img src={knexIcon} />
      {`Knex.js ${knexInfo.version || ''}`}
    </a>

    <span>
      <img src={clientIcons[client.id]} />
      <select
        name={'client'}
        onChange={setClient}
        value={client.id}
      >
        {
          KNEX_CLIENTS.map(({id, name}) => <option key={id} value={id}>{name}</option>)
        }
      </select>
    </span>

    <div className={s.spacer} />

    <a
      href='https://github.com/archytechtor/knex'
      rel='external noopener noreferrer'
      target='_blank'
    >
      <img src={github} />
      {'GitHub'}
    </a>
  </div>
);

const mapStore = ({EditorStore}) => {
  return {
    client: EditorStore.client,
    setClient: EditorStore.setClient
  };
};

OptionsBar.propTypes = {
  client: PropTypes.object,
  setClient: PropTypes.func
};

export default magic(OptionsBar, {store: mapStore, styles: s});