import {makeObservable, observable, action, reaction} from 'mobx';
import knex from 'knex';
import {decodeFromUrl, KNEX_CLIENTS} from '@utils';
import * as sqlFormatter from 'sql-formatter';

class EditorStore {
  SMALL_WIDTH = 700;

  containerRef;
  isSmall = false;

  code = [
    `// knex code`,
    `knex('users')`,
    `  .select('id', 'fullname')`,
    `  .whereIn('id', [1, 2, 3])`,
    `  .whereNull('deletedAt')`,
    `  .orderBy('fullname')`
  ].join('\n');

  sql;

  client = KNEX_CLIENTS[0];

  disposers = [];

  constructor() {
    makeObservable(this, {
      containerRef: observable,
      code: observable,
      sql: observable,
      client: observable,

      setRef: action,
      setCode: action,
      setSql: action,
      setClient: action
    });

    this.resizeObserver = new ResizeObserver(this.calculate);

    this.disposers = [
      reaction(
        () => this.containerRef,
        () => this.resizeObserver.observe(this.containerRef)
      ),
      reaction(
        () => [this.code, this.client],
        () => this.onCodeChange()
      )
    ];

    this.onCodeChange();
  }

  setRef = (ref) => {
    this.containerRef = ref;
  };

  setIsSmall = (state) => {
    this.isSmall = state;
  };

  setCode = (code) => {
    this.code = code;
  };

  setSql = (sql) => {
    this.sql = sql;
  };

  setClient = ({target}) => {
    this.client = KNEX_CLIENTS.find(({id}) => id === target.value) ?? this.client;
  };

  formatTypes = (types) => {
    const oldDeclare = [
      'declare function knex<TRecord extends {} = any, TResult = unknown[]>(',
      '  config: Knex.Config | string',
      '): Knex<TRecord, TResult>;'
    ].join('\n');

    const newDeclare = [
      'declare const knex: Knex.Client & (<TRecord extends {} = any, TResult = unknown[]>(',
      '  config: Knex.Config | string',
      ') => Knex<TRecord, TResult>);'
    ].join('\n');

    return types
      .replace(/^import .*$/gmu, '')
      .replace(/[^ ]export /gu, ' ')
      .replace(oldDeclare, newDeclare);
  };

  calculate = (entry) => {
    const [{target: container} = {}] = entry || [];

    if (!container) {
      return;
    }

    const containerWidth = container.offsetWidth;

    if (containerWidth >= this.SMALL_WIDTH) {
      return this.setIsSmall(false);
    }

    return this.setIsSmall(true);
  };

  onCodeChange = () => {
    window.knex = knex({client: this.client.id});

    try {
      let sql = '--- generated SQL code\n';
      // eslint-disable-next-line
      let code = eval(this.code).toQuery();

      try {
        code = sqlFormatter.format(code, {language: this.client.formatter});
      } catch(err) {
        sql += `--- sqlFormatter failed to run: ${this.errorHandlers(err)}\n`;
      }

      this.setSql(`${sql}${code}\n`);
    } catch(err) {
      this.setSql(`--- ${this.errorHandlers(err)}\n`);
    }
  };

  getDataFromPath = (location) => {
    if (location.hash) {
      const hash = location.hash.replace('#', '');

      this.setCode(hash ? decodeFromUrl(hash) : this.code);

      const queryParams = new URLSearchParams(location.search);

      return this.setClient({
        target: {
          value: queryParams.get('c') || this.client.id
        }
      });
    }

    const [search, hash] = location.search.split('%23');

    this.setCode(hash ? decodeFromUrl(hash) : this.code);

    const queryParams = new URLSearchParams(search);

    return this.setClient({
      target: {
        value: queryParams.get('c') || this.client.id
      }
    });
  };

  errorHandlers = (err) => {
    const DEFAULT_ERROR = `TypeError: SQL code can't be generated cause Knex code is incorrect`;

    const error = err?.toString() ?? err;

    if (error?.includes(`toQuery is not a function`)) {
      return DEFAULT_ERROR;
    }

    if (error?.includes(`Cannot read properties of undefined (reading 'toQuery')`)) {
      return DEFAULT_ERROR;
    }

    return error;
  };
}

export default EditorStore;